import { ThemeContext } from 'hooks/useTheme'
import React, { useContext, useEffect, useState } from 'react'
import LightModeIcon from '@mui/icons-material/LightMode'
import DarkModeIcon from '@mui/icons-material/DarkMode'
import styled from '@emotion/styled'

const DarkModeToggle: React.FC = () => {
  const { theme, setTheme } = useContext(ThemeContext)!

  const [isTransitioning, setIsTransitioning] = useState(false)
  const [showDarkModeIcon, setShowDarkModeIcon] = useState(theme === 'dark')

  useEffect(() => {
    setShowDarkModeIcon(theme === 'dark')
  }, [theme])

  const toggleTheme = () => {
    setIsTransitioning(true)

    setTimeout(() => {
      setTheme(prevTheme => (prevTheme === 'light' ? 'dark' : 'light'))

      setIsTransitioning(false)
    }, 200)

    setTimeout(() => {
      setShowDarkModeIcon(prevShowDarkModeIcon => !prevShowDarkModeIcon)
    }, 200)
  }

  return (
    <>
      <ModeButton onClick={toggleTheme}>
        {isTransitioning ? (
          <TransitionIcon>
            {showDarkModeIcon ? (
              <DarkModeIcon
                fontSize="large"
                sx={{ color: theme === 'light' ? 'black' : 'white' }}
              />
            ) : (
              <LightModeIcon
                fontSize="large"
                sx={{ color: theme === 'light' ? 'black' : 'white' }}
              />
            )}
          </TransitionIcon>
        ) : (
          <InstantIcon>
            {theme === 'light' ? (
              <DarkModeIcon
                fontSize="large"
                sx={{ color: theme === 'light' ? 'black' : 'white' }}
              />
            ) : (
              <LightModeIcon
                fontSize="large"
                sx={{ color: theme === 'light' ? 'black' : 'white' }}
              />
            )}
          </InstantIcon>
        )}
      </ModeButton>
    </>
  )
}

export default DarkModeToggle

const ModeButton = styled.div`
  cursor: pointer;
  margin: 10px 20px 5px 0px;
`

const TransitionIcon = styled.div`
  opacity: 0;
`

const InstantIcon = styled.div`
  opacity: 2;
`
